import React from 'react'
import { Link } from 'react-router-dom';

import styles from './Sidebar.module.scss';
import SidebarItem from './SidebarItem';

export type MenuData = {
	name: string,
	list: string[],
	link?: string,
	listLink?: string[]
}

const menu: MenuData[] = [
	{
		name: '',
	 	list: [],
		link: '/'
	}, 
	{
		name: '',
		list: ['  ', ' ', '  ', '  ', '  ', ' '],
		listLink: ['/search/goods', '/search/brands', '/search/sellers', '/search/category', '/search/favourites', '/search/history'],
		link: '/search'
	}, 
	{
		name: '',
		list: [],
		link: '/products_list'
	}, 
	{
		name: '',
		list: [''],
		link: '/goods_list'
	},
	{
		name: '',
		list: [''],
		link: '/pair_settings'
	}, 
	{
		name: '',
		list: [],
		link: '/notifications'
	},
	{
		name: ' ',
		list: ['', ' ', ' ', ' ', ' ', ''],
		link: '/inventory_changes'
	}, 
	{
		name: '',
		list: [],
		link: '/tags'
	}
];

const Sidebar: React.FC = () => {

	const [activePage, setActivePage] = React.useState(0);

	const [isHover, setIsHover] = React.useState(false);
	

	function setPage(i: number) {
		setActivePage(i);
	}

	const off = () => {
		setIsHover(false)
	}

	return ( 
		<div className={isHover ? styles.wrapper + " " + styles.active : styles.wrapper} onMouseLeave={off}>
			<Link to='/' className={styles.logo}>
				Smart<br/>Sell
			</Link>
			<div className={styles.sidebarItemWrapper}>
				{menu.map((item, i) => {
					return <SidebarItem 
								onClick={setPage} 
								item={item} i={i} key={i} 
								activePage={activePage}
								isHover={isHover}
								setIsHover={setIsHover}
								/>
				})}
			</div>
		</div>
	)
}

export default Sidebar;